<template>
    <el-container>
        <el-header class="options">
            <div class="option-header">
                <div class="left">
                    <div class="item" v-for="(item, index) in data.leftMenu" :key="index">
                        <span>{{item.name}}</span>
                        <!-- input -->
                        <div v-if="!item.type">
                            <el-input v-if="item.module === 'name'" v-model="name" :placeholder="item.placeholder"/>
                            <el-input v-if="item.module === 'title'" v-model="title" :placeholder="item.placeholder"/>
                            <el-input v-if="item.module === 'count'" v-model="count" :placeholder="item.placeholder"/>
                            <el-input v-if="item.module === 'price'" v-model="price" :placeholder="item.placeholder"/>
                            <el-input v-if="item.module === 'contactPerson'" v-model="contactPerson" :placeholder="item.placeholder"/>
                            <el-input v-if="item.module === 'mobile'" v-model="mobile" :placeholder="item.placeholder"/>
                        </div>
                        <!-- date -->
                        <div v-if="item.type === 'date'">
                            <el-date-picker v-if="item.module === 'beginTime'" v-model= "beginTime" :placeholder="item.placeholder" type="date"/>
                            <el-date-picker v-if="item.module === 'endTime'" v-model= "endTime" :placeholder="item.placeholder" type="date"/>
                       </div>
                        <!-- select -->
                        <div v-if="item.type === 'select'">
                            <!-- 学校名称 -->
                            <el-select v-if="item.module === 'schoolId'" v-model="schoolId" placeholder="全部">
                                <el-option
                                    v-for="item in item.options"
                                    :key="item.id"
                                    :label="item.dValue"
                                    :value="item.id"
                                ></el-option>
                            </el-select>

                            <!-- 班级名称 -->
                            <el-select v-if="item.module === 'classId'" v-model="classId" placeholder="全部">
                                <el-option
                                    v-for="item in item.options"
                                    :key="item.id"
                                    :label="item.dValue"
                                    :value="item.id"
                                ></el-option>
                            </el-select>

                            <!-- 角色列表 -->
                            <el-select v-if="item.module === 'roleId'" v-model="roleId" placeholder="全部">
                                <el-option
                                    v-for="item in item.options"
                                    :key="item.id"
                                    :label="item.dValue"
                                    :value="item.id"
                                ></el-option>
                            </el-select>                
                            <el-select v-if="item.module === 'role'" v-model="role" placeholder="全部">
                                <el-option
                                    v-for="item in item.options"
                                    :key="item.id"
                                    :label="item.dValue"
                                    :value="item.id"
                                ></el-option>
                            </el-select>            

                            <el-select v-if="item.module === 'select'" v-model="select" placeholder="全部">
                                <el-option
                                    v-for="item in item.options"
                                    :key="item.id"
                                    :label="item.dValue"
                                    :value="item.id"
                                ></el-option>
                            </el-select>                            
                       </div>
                    </div>
                </div>
                <div class="right" v-show="data.rightMenu && data.rightMenu.length > 0">
                    <el-button v-for="(item, index) in data.rightMenu" @click="btnClick(item)" class="btn" :key="index" type="primary">{{item.name}}</el-button>
                </div>
            </div>
        </el-header>
    </el-container>
</template>
<script>
import { DateDay } from 'utils/formData'
export default {
  name: "options",
  props:['data'],
  data() {
    let obj = {};
    this.data.leftMenu.forEach(item => {
        obj[item.module] = ''
    })
    return obj;
  },
  methods: {
    btnClick(item) {
        const params = {
            data: item,
            id: item.id
        }
        this.$emit('change', params);
    },
    getData(){
        let obj = {};
        this.data.leftMenu.forEach(item => {
            let str = this[item.module];
            // 如果是日期修改
            if(item.type === 'date' && str){
                str = DateDay(str)
            }
            obj[item.module] = str
        })
        return obj;
    }
  }
};
</script>
<style lang="less" scoped>
@import url("./../../assets/less/color.less");
.options {
    border-bottom: 1px solid @border-gray;
    height: inherit !important;
    .option-header{
        display: flex;
        align-items: center;
        justify-content: space-between;
        padding: 15px 0;
        .left{
            display: flex;
            flex-wrap: wrap;
            .item{
                display: flex;
                margin: 10px;
                span{
                    display: inline-block;
                    width: 115px;
                    height: 40px;
                    text-align: right;
                    line-height: 40px;
                }
            }
        }
        .right{
            display: flex;
        }
    }   
}
</style>